import Dropzone from 'vue2-dropzone'


export default {
	name : 'v-upload',
	data() {
		return {
			showRemoveLink : true,
			uploadMultiple : true,
			language       : {
				dictDefaultMessage           : '',
				dictFallbackMessage          : '',
				dictFallbackText             : '',
				dictInvalidFileType          : '',
				dictFileTooBig               : '{{filesize}}, {{maxFilesize}}',
				dictResponseError            : '{{statusCode}}: ',
				dictCancelUpload             : '',
				dictCancelUploadConfirmation : '',
				dictRemoveFile               : '',
				dictMaxFilesExceeded         : ''
			}
		}
	},
	components : {
		Dropzone
	},
	props : {
		uri : {
			type    : String,
			default : 'http://localhost:8082/upload'
		},
		thumbnailWidth : {
			type    : Number,
			default : 200
		},
		thumbnailHeight : {
			type    : Number,
			default : 200
		},
		// 
		maxNumberOfFiles: {
			type    : Number,
			default : 5
		},
		// 
		autoProcessQueue : {
			type    : Boolean,
			default : true
		},
		showSuccess : {
			type    : Function,
			default : function(file, result) {
			}
		},
		addfiles : {
			type    : Function,
			default : function(files) {
			}
		},
		successfiles : {
			type    : Function,
			default : function(files, response) {
			}
		},
		sendingfile : {
			type    : Function,
			default : function(files, xhr, formData) {
			}
		},
		errorfile : {
			type    : Function,
			default : function(file) {
			}
		}
	},
	methods : {
		removeAllFiles() {
            this.$refs.myVueDropzone.removeAllFiles()
        },
        addFile(file) {
        },
        setOption(optionname, value) {
			this.$refs.myVueDropzone.setOption(optionname, value)
		}
	}
}


